import quickBtn from '@/components/mbbo/quickBtn/quickBtn.vue';
import navigation from '@/components/mbbo/navigation/navigation.vue';
import rQrCode from '@/components/common/RHX/rQrCode/rQrCode';
import rClipBoard from '@/components/common/RHX/rClipBoard/rClipBoard';
import { mapMutations, mapGetters } from 'vuex';
export default {
	data() {
		return {
			list: null,
			tapIndex: 0,
			dataLists:null, // 链接上的参数
			btnList:[
				{
					type: 0,
					tempType: 0,
					name: '我的说明书',
					style: {}
				},
				{
					type: 1,
					tempType: 0,
					name: '继续创建',
					style: {}
				},
				{
					type: 2,
					tempType: 0,
					name: '立即投放',
					style: {
						backgroundImage: 'linear-gradient(134deg, #15D49F 0%, #57D6CF 100%)',
						color:  '#FFFFFF'
					}
				}
			]
		};
	},
	components: {
		quickBtn,
		navigation,
		rQrCode,
		rClipBoard
	},
	onLoad(option) {
		this.dataLists = this.urlCrypto(option.urlParams, 1);
		console.log(this.dataLists, '----------------------------------------------------')
	},
	created() {
		this.Statistics();
	},
	methods: {
		tapRQrCode (e) {
			console.log(e, '触发二维码组件');
		},
		tapRClicpBoard(e) {
			console.log(e, '剪切板');
		},
		Statistics(){
			if (uni.getStorageSync('landRegist')) {
				let landRegistLG = JSON.parse(uni.getStorageSync('landRegist')); // 读取缓存的用户信息
				console.log(landRegistLG.user.id);
				let params = {}; // 请求总数居时 参数为空
				uni.showLoading({ // 展示loading
					title: '加载中'
				});
				uni.request({
					url: this.api2 + '/rest-rp/putIn/760', //接口地址。
					method: 'GET',
					header: {
						Authorization: "Bearer " + landRegistLG.token //将token放到请求头中
					},
					success: (response) => {
						uni.hideLoading();
						console.log(response.data);
						this.list = response.data.content;
					},
					fail: (error) => {
						uni.hideLoading(); // 隐藏 loading
						uni.showToast({
							title: '网络繁忙，请稍后',
							icon: 'none',
							duration: 1000
						});
						console.log(error, '网络繁忙，请稍后');
					}
				});
			}
		},
		goToPutInBusiness() {
			console.log('to投放商机');
			if (this.list.rpCompanyAuth == 0) {
				uni.showModal({
					title: '提示',
					content: '您的公司还未认证，无法进行广告投放，快去提交公司认证信息吧～',
					confirmColor: ' #02C2A2',
					confirmText: '去认证',
					cancelText: '暂不认证',
					success: function(res) {
						if (res.confirm) {
							console.log('用户点击确定');
							
						} else if (res.cancel) {
							console.log('用户点击取消');
						}
					}
				});
			} else {
				if (uni.getStorageSync('landRegist')) {
					let landRegistLG = JSON.parse(uni.getStorageSync('landRegist')); // 读取缓存的用户信息
					console.log(landRegistLG.user.id);
					let params = {
						page: String(1),
						userId: 760
					}; // 请求总数居时 参数为空
					uni.showLoading({
						// 展示loading
						title: '加载中'
					});
					uni.request({
						url: this.api2 + '/rest-rp/putIn/selectInstList', //接口地址。
						data: params,
						method: 'POST',
						header: {
							Authorization: 'Bearer ' + landRegistLG.token //将token放到请求头中
						},
						success: response => {
							uni.hideLoading();
							console.log(response.data);
							this.putInBusinessData = response.data.content.list;
							// this.list = response.data.content
							this.$store.commit('setputInBusinessData', this.putInBusinessData); // 更新vuex
							if (response.data.ret == 200) {
								uni.navigateTo({
									url: '/modules/aLittle/putInBusiness/putInBusiness'
								});
							}
						},
						fail: error => {
							uni.hideLoading(); // 隐藏 loading
							uni.showToast({
								title: '网络繁忙，请稍后',
								icon: 'none',
								duration: 1000
							});
							console.log(error, '网络繁忙，请稍后');
						}
					});
				}
			}
		},
		btnItems (e) {
			console.log(e, '按钮');
			if (e.type === 0) {
				console.log('我的说明书');
				uni.navigateTo({
					url: '/modules/InstructionsPrice/myproduct/myproduct-shu'
				});
			} else if (e.type === 1) {
				console.log('继续创建');
				uni.navigateTo({
					url: '/modules/createBusiness/selectTemplate'
				});
			} else if (e.type === 2) {
				console.log('发布');
				this.goToPutInBusiness();
			}
		}
	},
};
